import { GatsbyImage, getImage } from "gatsby-plugin-image";
import React from "react";
import * as styles from './Lesson.module.css';

import Layout from '../components/layout/Layout'
import Back from "../components/back/Back";

const Lesson = ({pageContext}) => {
    
    const data = pageContext.data;
    const academy = data[0].book.academy.name;
    const name = data[0].book.name;
    const subheading = data[0].book.subheading;
    const cover = data[0].book.cover.localFile;
    const coverImage = getImage(cover);
    
    console.log(data);

    return(
        <Layout>
            <article className={styles.lessonContainer}>
                <div className={styles.coverContainer}>
                    <GatsbyImage className={styles.cover} image={coverImage} objectFit="contain"/>
                    <div className={styles.info}>
                        <h2 className={styles.title}>{`${name} : ${subheading}`}</h2>
                        <p className={styles.blurb} >Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur.</p>
                    </div>
                </div>
                <div className={styles.videoContainer}>
                    {data.map((item) => {
                        console.log(item)
                        return(
                            <figure>
                                <iframe className={styles.video} src={item.link} title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
                                <figcaption className={styles.videoCaption}>{item.name}</figcaption>
                            </figure>
                        )
                    })}

                    <figure>
                        <iframe className={styles.video} src="https://www.youtube.com/embed/5iNWLMu64KI" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
                        <figcaption className={styles.videoCaption}>Three</figcaption>
                    </figure>                    
                </div>

            </article>
        </Layout>
    )
   
}

export default Lesson;

